<template>
  <div class="certification">
    <div class="record-bar">
      <van-nav-bar
        :title="$t('RealName')"
        left-arrow
        @click-left="linkHome"
      />
    </div>
    <div class="content-box">
      <div class="title">{{$t('CertificateInformation')}}</div>
      <div class="content">
        <van-cell :title="$t('Name')" center>
          <div slot="default">{{name}}</div>
        </van-cell>
        <van-cell :title="$t('BankOfDeposit')" center>
          <div slot="default">{{bankName}}</div>
        </van-cell>
<!--        <van-cell class="sex" center>-->
<!--          <div slot="title" class="sex-box">-->
<!--            <div>性别</div>-->
<!--            <div>{{Gender}}</div>-->
<!--          </div>-->
<!--        </van-cell>-->
<!--        <van-cell :title="$t('typeCertificate')" center>-->
<!--&lt;!&ndash;          <div slot="default">{{idcardtype}}</div>&ndash;&gt;-->
<!--          <div slot="default">身份证</div>-->
<!--        </van-cell>-->
        <van-cell :title="$t('Bankcard')" center>
          <div slot="default">{{id}}</div>
        </van-cell>
      </div>
    </div>
  </div>
</template>

<script>
  import  EP  from '../../../utils/encryption'
export default {
  name: 'certification',
  data () {
    return {
      // 性别
      Gender: '完善性别信息',
      // 姓名
      name:'',
      // 身份证号码
      id:'',
      bankName:'',
      // 证明类型
      idcardtype:''
    }
  },
  created(){
    this.name = "*"+localStorage.getItem("name").substr(1);
    this.bankName = localStorage.getItem("bank")
    let idcard = EP.decrypt(localStorage.getItem("bankAccount"),'')
    this.Gender = this.getSexByIdNO(idcard)
    // this.id = idcard.substr(0,1)+"****************"+idcard.substr(idcard.length-1)
    this.id =idcard
    this.idcardtype = localStorage.getItem("idcardtype")
  },
  methods: {
    // 返回上级
    linkHome () {
      this.$router.go(-1)
    },
    // 根据身份证判断男女
    getSexByIdNO(IdNO){
      if (IdNO.length==18) {
        return IdNO.charAt(16)%2==0?"女":"男";
      }else if(IdNO.length==15){
        return IdNO.charAt(14)%2==0?"女":"男";
      }else{
        return "";
      }
  }
  }
}
</script>

<style scoped lang="scss">
  @import "../../../style/my";
</style>



